.project-block {
    width: auto;
    height: auto;
    margin-bottom: 50px;
    padding: 30px;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
}

.slider {
    width: 60vw;
    height: 60vh;
    margin: 0 auto;
    opacity: 0;
    z-index: 1;
    border: 1px solid black;
    display: none;
    transition: opacity 0.5s;
}

.showing {
    opacity: 1;
    z-index: 2;
    display: block;
}

.project-header {
    text-align: center;
    width: 60vw;
    margin-bottom: 50px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 45px;
}

.project-description {
    text-align: center;
    width: 60vw;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 25px;
}

#previous {
    position: absolute;
    top: 50%;
    left: 9%;
    transform: rotate(180deg);
    width: 50px;
    background-color: rgba(255, 255, 255, 0);
    border-radius: 50px;
    border: 1px solid rgba(255, 255, 255, 0.0);
    font-size: 40px;
    outline: none;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    padding-bottom: 10px;
}

#previous:hover {
    background-color: black;
    color: white;
}

#next {
    padding-bottom: 10px;
    width: 50px;
    position: absolute;
    top: 50%;
    right: 9%;
    background-color: rgba(255, 255, 255, 0);
    border: 1px solid rgba(255, 255, 255, 0.0);
    border-radius: 50px;
    font-size: 40px;
    outline: none;
    cursor: pointer;
}

#next:hover {
    background-color: black;
    color: white;
}

