.container-morse {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    min-height: 250px;
    background-color: #FF8300;
    padding: 30px 0;
}

#morse-output {
    width: 60vw;
    height: auto;
    font-size: 60px;
    color: black;
    margin: 0 auto;
}

#morse-input {
    border: 1px solid black;
    font-size: 30px;
    background-color: white;
    color: black;
    text-overflow: ellipsis;
    margin: 50px auto 0px auto;
    height: 50px;
    width: 60vw;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.morse-input::-webkit-input-placeholder {
    color: black;
    text-align: center;
    opacity: 0.5;
}

.Morse-header {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    text-align: center;
    margin: 0 auto;
    padding-top: 32px;
    color: black;
}
