.contact {
    width: auto;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-bottom: 50px;
}

.about-me {
    display: block;
    text-align: center;
    margin: 50px;
    width: 60vw;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 25px;
}

.my-photo {
    width: 200px;
    height: 200px;
    margin: 50px;
    border-radius: 100px;
    background: url("../Photo/Contact-photo.jpg") 50% 50% no-repeat;
    background-size: cover;
}

.links {
    width: auto;
    height: 50px;
    display: flex;
    justify-content: space-around;
}

.Vk {
    width: 50px;
    height: inherit;
    background: url(../Photo/vk.png) 50% 50% no-repeat;
    background-size: contain;
    margin: 5px;
}

.Email {
    width: 50px;
    height: inherit;
    background: url(../Photo/email.png) 50% 50% no-repeat;
    background-size: contain;
    margin: 5px;
}

.WhatsUp {
    width: 50px;
    height: inherit;
    background: url(../Photo/whatsup.png) 50% 50% no-repeat;
    background-size: contain;
