.skill-block {
    width: auto;
    height: auto;
}

.block-inside {
    width: 60vw;
    min-width: 400px;
    height: auto;
    margin: 0 auto;
    position: relative;
    padding: 50px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-template-rows: repeat(auto-fit, auto);
    grid-gap: 20px;
}

.skill-name {
    padding: 25px;
    text-align: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    width: 30vw;
}

.skill-description {
    width: auto;
    display: block;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 20px;
    min-width: 300px;
    width: 30vw;
}

.skill-picture {
    width: 60vw;
    min-width: 270px;
    height: auto;
    display: block;
    border: 1px solid black;
    border-radius: 5px;
}

.about-me-block {
    background-color: #FF8300;
    width: auto;
    height: auto;
}

.about-me-inside {
    width: 60vw;
    height: auto;
    margin: 0 auto;
    position: relative;
    padding: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.about-me-pic {
    width: 25vw;
    min-width: 200px;
    height: 400px;
    margin: 10px;
    background: url(../Photo/me.jpg) 50% 50% no-repeat;
    background-size: cover;
    border-radius: 10px;
    box-shadow: 0px 0px 1px 1px black;
}

.text-about-me {
    display: inline-block;
    width: 30vw;
    min-width: 200px;
    height: auto;
    margin: 10px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 22px;
}

