.header {
    width: auto;
    height: 55vh;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
}

.currentTime {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;
    font-size: 30px;
    text-shadow: 0.5px 0.5px 1px black;
}

.introduction {
    margin-bottom: 10px;
    width: auto;
    text-align: center;
    font-size: 23px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;
    text-shadow: 0.5px 0.5px 1px black;
}

input {
    width: 250px;
    height: 30px;
    margin: 15px;
    border-radius: 5px;
    font-size: 20px;
}

.questionsBlock {
    width: auto;
    display: flex;
    height: 47vh;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: #FF8300;
}

.undertext {
    font-size: 20px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;
    margin: -10px 0 50px 0;
    display: block;
    width: auto;
    height: auto;
}

.submit {
    width: 100px;
    height: 35px;
    margin-top: 16px;
    border-radius: 5px;
    font-size: 15px;
}

#choice-block {
    display: none;
}

