.choice-block {
    height: 100vh;
    width: auto;
    padding-top: 50px;
}

.choice-inside {
    width: inherit;
    height: inherit;
    padding: 120px 0 130px 0;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 20px;
}

.choosing {
    width: 100vw;
    height: 60vh;
    display: flex;
}

.choice-text {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 25px;
}

#Text {
    color: white;
    font-size: 3vw;
    font-weight: bold;
    display: inline;
    text-shadow: #BF7930 1px 1px 0, #BF7930 -1px -1px 0,
    #BF7930 -1px 1px 0, #BF7930 1px -1px 0;
}

#Text2 {
    color: white;
    font-size: 3vw;
    font-weight: bold;
    display: inline;
    text-shadow: #BF7930 1px 1px 0, #BF7930 -1px -1px 0,
    #BF7930 -1px 1px 0, #BF7930 1px -1px 0;
}

#choice-pic-1 {
    background-color: black;
    width: 50vw;
    height: inherit;
    background: url("../Photo/frontend.png") 5% 19% no-repeat;
    border: 1px solid #BF7930;
    padding-top: 20px;
}

#choice-pic-2 {
    background: url(../Photo/Commercial.png) 50% 8% no-repeat;
    width: 50vw;
    height: inherit;
    border: 1px solid #BF7930;
    padding-top: 20px;
}

#block1 {
    display: none;
    width: auto;
    height: auto;
}

#block2 {
    display: none;
    width: auto;
    height: auto;
}